<template>
  <div class="join-comp">
    <div class="button-container">
      <div style="margin-left:auto !important"></div>
      <v-btn
        color="#42aaee"
        small
        dark
        elevation="0"
        @click="onJoinConditionBtnClick"
      >Add Join Condition</v-btn>
    </div>
    <VuePerfectScrollbar class="scroll-area" @ps-scroll-y="scrollHandle">
      <div class="join-condtion" v-if="isJoinPanelOpen && selectedElement===-1">
        <div class="join-condition-header">
          <span style="padding-left:8px;color:#42aaee">{{ properties.length+1}}. Join condition</span>
          <div style="margin-left:auto !important"></div>
          <v-icon
            v-if="!isJoinPanelOpen"
            @click="isJoinPanelOpen=true"
            color="#42aaee"
          >keyboard_arrow_right</v-icon>
          <v-icon v-else color="#42aaee" @click="onHideJoinPanel()">keyboard_arrow_down</v-icon>
        </div>
        <join-condition-panel
          :props="defaultProp"
          :hide="hide"
          @on-update-condition="onUpdateCondition"
          :isReadOnly="properties.length!==1"
        ></join-condition-panel>
      </div>

      <div v-for="(item,i) in  properties" :key="i">
        <div class="join-condtion">
          <div class="join-condition-header">
            <span style="padding-left:8px;color:#42aaee">{{i+1}}. Join condition</span>
            <div style="margin-left:auto !important"></div>
            <v-icon
              v-if="isJoinPanelOpen && selectedElement===i"
              color="#42aaee"
              @click="onHideJoinPanel()"
            >keyboard_arrow_down</v-icon>
            <v-icon v-else @click="onJoinConditionItemClick(i)" color="#42aaee">keyboard_arrow_right</v-icon>
          </div>
          <join-condition-panel
            v-if="isJoinPanelOpen && selectedElement===i"
            :props="properties[i]"
            :hide="hide"
            @on-update-condition="onUpdateCondition"
            :isReadOnly="i!==0"
          ></join-condition-panel>
        </div>
      </div>
    </VuePerfectScrollbar>
  </div>
</template>
<script>
import JoinConditonPanel from "./JoinConditonPanel";
import VuePerfectScrollbar from "vue-perfect-scrollbar";
export default {
  props: {
    type: String,
    properties: [Object, Array]
  },
  components: {
    "join-condition-panel": JoinConditonPanel,
    VuePerfectScrollbar
  },
  data() {
    return {
      isJoinPanelOpen: false,
      hide: false,
      selectedElement: -1,
      defaultProp: {},
      isScrolling: false
    };
  },
  methods: {
    scrollHandle(evt) {
      console.log(evt);
      this.isScrolling = true;
    },
    onHideJoinPanel() {
      this.hide = !this.hide;
    },
    onJoinConditionBtnClick() {
      this.isJoinPanelOpen = true;
      this.selectedElement = -1;
      let condition = {
        leftTable: "",
        rightTable: "",
        joinType: "",
        columnCondition: [
          {
            leftCol: "",
            rightCol: "",
            andOr: ""
          }
        ]
      };
      this.defaultProp = condition;
    },
    onJoinConditionItemClick(i) {
      this.isJoinPanelOpen = true;
      this.selectedElement = i;
    },
    onUpdateCondition(condition) {
      this.isJoinPanelOpen = false;
      if (this.selectedElement === -1) this.properties.push(condition);
      else this.properties[this.selectedElement] = { ...condition };
    }
  }
};
</script>
<style scoped>
.button-container {
  display: flex;
  margin: 0 0 16px 0;
}
.join-comp {
  padding: 16px;
}
.join-condtion {
  border: 0.5px solid #ccc;
}
.join-condition-header {
  display: flex;
}
.scroll-area {
  position: relative;
  height: 300px;
  overflow: hidden;
}
</style>