<template>
  <div class="join-condition-panel">
    <div class="join-condition-panel-table">
      <v-combobox
        v-model="leftTable"
        :items="items"
        label="Left Table"
        placeholder="Select"
        class="mx-1 join-condition-panel-combobox"
        :readonly="isReadOnly"
      ></v-combobox>
      <v-combobox
        v-model="joinType"
        :items="items"
        label="Join Type"
        placeholder="Select"
        class="mx-1 join-condition-panel-combobox"
      ></v-combobox>
      <v-combobox
        v-model="rightTable"
        :items="items"
        label="Right Table"
        placeholder="Select"
        class="mx-1 join-condition-panel-combobox"
      ></v-combobox>
    </div>
    <span style="color:#8a8585">Columns</span>
    <div v-for="(item,i) in columnCondition" :key="i">
      <div class="join-condition-panel-table">
        <v-combobox
          v-model="item.leftCol"
          :items="items"
          label="Left Column"
          placeholder="Select"
          class="mx-1 join-condition-panel-combobox"
        ></v-combobox>
        <v-combobox
          v-model="item.rightCol"
          :items="items"
          label="Right Column"
          placeholder="Select"
          class="mx-1 join-condition-panel-combobox"
        ></v-combobox>
        <v-combobox
          v-model="item.andOr"
          :items="items"
          label="AND/OR"
          placeholder="Select"
          class="mx-1 join-condition-panel-combobox"
          @change="onChangeAndOr(i)"
          clearable
          @click:clear="onClearAndOr(i)"
        ></v-combobox>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    props: [Object, Array],
    hide: Boolean,
    isReadOnly: Boolean
  },
  data() {
    return {
      items: ["Gaming", "Programming", "Vue", "Vuetify"],
      model: "",
      columnCondition: {},
      leftTable: "",
      joinType: "",
      rightTable: ""
      // isReadOnly:true
    };
  },
  mounted() {
    this.leftTable = this.props.leftTable;
    this.rightTable = this.props.rightTable;
    this.joinType = this.props.joinType;
    this.columnCondition = this.props.columnCondition;
  },
  watch: {
    hide: {
      handler(newVal) {
        this.onHide();
      }
    },
    isReadOnly: {
      handler(newVal) {
        console.log(newVal);
      }
    }
  },
  methods: {
    onHide() {
      let condition = {
        leftTable: this.leftTable,
        rightTable: this.rightTable,
        joinType: this.joinType,
        columnCondition: this.columnCondition
      };
      this.$emit("on-update-condition", condition);
    },
    onClearAndOr(i) {
      this.columnCondition.splice(i + 1);
    },
    onChangeAndOr(i) {
      if (this.columnCondition[i].andOr === undefined)
        this.columnCondition[i].andOr = "";
      if (
        this.columnCondition.length === i + 1 &&
        this.columnCondition[i].andOr !== ""
      ) {
        let newItem = {
          leftCol: "",
          rightCol: "",
          andOr: ""
        };
        this.columnCondition.push(newItem);
      }
    }
  }
};
</script>
<style scoped>
.join-condition-panel {
  display: flex;
  flex-direction: column;
}
.join-condition-panel-combobox >>> label {
  color: #42aaee !important;
}
.join-condition-panel-combobox >>> i {
  color: #42aaee !important;
}
.join-condition-panel-table {
  display: flex;
  flex-direction: row;
}
.test {
  display: flex !important;
  flex-direction: column;
}
</style>