<template>
  <!-- <div class="circle">
  <svg class="circle__svg" viewBox="0 0 100 100" xmlns="http://www.w3.org/2000/svg">
    <circle class="circle__svg-circle" cx="50" cy="50" r="45"/>
  </svg>
  </div>-->
  <keep-alive>
    <component
      @update-data="updateData"
      :is="componentInfo[dynamicComponent.type]!==undefined?componentInfo[dynamicComponent.type].text+'Component':'EmptyComponent'"
      :type="componentInfo[dynamicComponent.type]!==undefined?componentInfo[dynamicComponent.type].text:''"
      :properties="dynamicComponent"
    ></component>
  </keep-alive>
</template>
 <script>
import SourceComponent from "./SourceComponent.vue";
import FilterComponent from "./FilterComponent.vue";
import JoinComponent from "./JoinComponent.vue";
import AlterComponent from "./AlterComponent.vue";
import TargetComponent from "./TargetComponent.vue";
import EmptyComponent from "./EmptyComponent.vue";
export default {
  components: {
    SourceComponent: SourceComponent,
    FilterComponent: FilterComponent,
    JoinComponent: JoinComponent,
    AlterComponent: AlterComponent,
    TargetComponent: TargetComponent,
    EmptyComponent: EmptyComponent
  },
  props: {
    dynamicComponent: Object
  },
  data() {
    return {
      componentInfo: {
        SOURCE: {
          text: "Source",
          type: "SOURCE",
          category: "input",
          url: "/icons/inout-white.svg"
        },
        TARGET: {
          text: "Target",
          type: "TARGET",
          category: "output",
          url: "/icons/output.svg"
        },
        FILTER: {
          text: "Filter",
          type: "FILTER",
          category: "general",
          url: "/icons/filter.svg"
        },
        JOIN: {
          text: "Join",
          type: "JOIN",
          category: "general",
          url: "/icons/join.svg"
        },
        ALTER: {
          text: "Alter",
          type: "ALTER",
          category: "general",
          url: "/icons/alter.svg"
        }
      }
    };
  },
  watch: {
    dynamicComponent: {
      handler(newV) {
        console.log(newV);
      }
    }
  },
  methods: {
    updateData(data) {
      this.$emit("update-data", data);
    }
  }
};
</script>

<style scoped>
.circle {
  height: 50px;
  margin-left: auto;
  margin-right: auto;
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  width: 50px;
}
.circle__svg {
  animation: 2s linear infinite both circle__svg;
}
@keyframes circle__svg {
  0% {
    transform: rotateZ(0deg);
  }
  100% {
    transform: rotateZ(360deg);
  }
}
.circle__svg-circle {
  animation: 1.4s ease-in-out infinite both circle__svg-circle;
  fill: transparent;
  stroke: #2f3d4c;
  stroke-dasharray: 285;
  stroke-linecap: round;
  stroke-width: 10px;
  transform-origin: 50% 50%;
}
@keyframes circle__svg-circle {
  0%,
  25% {
    stroke-dashoffset: 280;
    transform: rotate(0);
  }
  50%,
  75% {
    stroke-dashoffset: 75;
    transform: rotate(45deg);
  }
  100% {
    stroke-dashoffset: 280;
    transform: rotate(360deg);
  }
}
</style>