<template>
  <div class="workflow-div" :id="id"></div>
</template>
<script>
import go from "gojs";
export default {
  props: {
    addedNodeType: Object,
    refreshDiag: Boolean,
    workflow: Object
  },
  data() {
    return {
      id: "workflow-diagram",
      myDiagram: null,
      componentInfo: {
        SOURCE: {
          text: "Source",
          type: "SOURCE",
          category: "input",
          url: "/icons/inout-white.svg"
        },
        TARGET: {
          text: "Target",
          type: "TARGET",
          category: "output",
          url: "/icons/output.svg"
        },
        FILTER: {
          text: "Filter",
          type: "FILTER",
          category: "general",
          url: "/icons/filter.svg"
        },
        JOIN: {
          text: "Join",
          type: "JOIN",
          category: "general",
          url: "/icons/join.svg"
        },
        ALTER: {
          text: "Alter",
          type: "ALTER",
          category: "general",
          url: "/icons/alter.svg"
        }
      }
    };
  },
  mounted() {
    this.createDiagram();
    this.createNodes();
    this.createLinks();
  },
  watch: {
    workflow: {
      handler(newV, oldV) {
        this.createNodes();
        this.createLinks();
      }
    },
    addedNodeType: {
      handler(newV) {
        this.addNode(newV);
      }
    },
    refreshDiag: {
      handler() {
        let newJson = this.myDiagram.model.toJson();
        this.myDiagram.model = go.Model.fromJson(newJson);
        this.myDiagram.commandHandler.resetZoom();
      }
    }
  },
  methods: {
    test(id) {
      let _this = this;
      let node = _this.myDiagram.findNodeForKey(id);
      let shape = node.findObject("ELLIPSE");
      shape.stroke = "red";
      shape.strokeWidth = 5;
      shape.strokeCap = "round";
      shape.angle = 0;

      shape.strokeDashArray = [0, 39, 0, 117];
      let increment = false;
      setInterval(function() {
        let arr0 = shape.strokeDashArray[0];
        let arr1 = shape.strokeDashArray[1];
        let arr2 = shape.strokeDashArray[2];
        let arr3 = shape.strokeDashArray[3];

        //case 1

        /*  if (arr2 >= 117 && increment) {
          increment = false;
          
        } else if (arr2 <= 10 && !increment) {
          increment = true;
        } else {
        }
        arr2 = increment ? arr2 + 10 : arr2 - 10;
        shape.strokeDashArray = [arr0, arr1, arr2, arr3]; */

        // case 2

        if (arr2 >= 117 && arr0 < 40) {
          arr0 = arr1 + 10;
          arr1 = 0;
          // arr2=0
        } else {
          arr2 = arr2 > 117 ? 10 : arr2 + 10;
        }

        shape.strokeDashArray = [arr0, arr1, arr2, arr3];
      }, 50);
    },
    createDiagram() {
      let _this = this;
      var $ = go.GraphObject.make;
      _this.myDiagram = $(go.Diagram, _this.id, {
        layout: $(go.TreeLayout, {
          arrangement: go.TreeLayout.ArrangementVertical,
          comparer: go.LayoutVertex.smartComparer
        }),
        "undoManager.isEnabled": true
      });

      var portSize = new go.Size(8, 8);
      // _this.myDiagram.commandHandler.allowDelete=false;
      _this.myDiagram.commandHandler.canDeleteSelection = false;
      let inputTemplate = $(
        go.Node,
        "Vertical",
        new go.Binding("isShadowed", "isSelected").ofObject(),
        {
          selectionAdorned: false,
          shadowOffset: new go.Point(0, 0),
          shadowBlur: 15,
          shadowColor: "blue"
        },
        new go.Binding("location", "loc", go.Point.parse),
        $(
          go.Panel,
          "Horizontal",
          { portId: "" },
          $(go.Shape, "TriangleRight", {
            stroke: "#2196F3",
            strokeWidth: 1,
            desiredSize: portSize,
            fill: "#ffffff",
            portId: "left",
            toSpot: go.Spot.Left,
            toLinkable: true
          }),
          $(
            go.Panel,
            "Auto",
            $(
              go.Shape,
              "RoundedRectangle",
              {
                stroke: "#2196F3",
                width: 50,
                height: 50,
                fromEndSegmentLength: 0,
                toEndSegmentLength: 0,
                fill: "#ffffff",
                strokeWidth: 1.2
              }
              // new go.Binding("fill", "color")
            ),
            $(go.Shape, "Ellipse", {
              width: 40,
              height: 40,
              // margin: 4,
              fill: "#ffffff",
              name: "ELLIPSE",
              stroke: "#2196F3",
              strokeWidth: 1.2
              // background: "lightgreen"
            }),
            $(
              go.TextBlock,
              { text: "tst", font: "bold 7pt cursive", stroke: "#2196F3" },
              new go.Binding("text")
            )
          ),
          $(go.Shape, "TriangleRight", {
            stroke: "#2196F3",
            strokeWidth: 1,
            desiredSize: portSize,
            fill: "#ffffff",
            portId: "right",
            fromLinkable: true
          })
        )
        // $(go.TextBlock, { margin: 3, stroke: "red" }, new go.Binding("text"))
      );

      let outputTemplate = $(
        go.Node,
        "Vertical",
        new go.Binding("isShadowed", "isSelected").ofObject(),
        {
          selectionAdorned: false,
          shadowOffset: new go.Point(0, 0),
          shadowBlur: 15,
          shadowColor: "blue"
        },
        new go.Binding("location", "loc", go.Point.parse),
        $(
          go.Panel,
          "Horizontal",
          { portId: "" },
          $(go.Shape, "TriangleRight", {
            stroke: "#f39c12",
            strokeWidth: 1,
            desiredSize: portSize,
            fill: "white",
            portId: "left",
            toSpot: go.Spot.Left,
            toLinkable: true
          }),
          $(
            go.Shape,
            "RoundedRectangle",
            {
              fill: "white",
              stroke: "#f39c12",
              width: 50,
              height: 50,
              fromEndSegmentLength: 0,
              toEndSegmentLength: 0
            },
            new go.Binding("fill", "color")
          ),
          $(go.Shape, "TriangleRight", {
            stroke: "#ffffff",
            strokeWidth: 1,
            desiredSize: portSize,
            fill: "white",
            portId: "right",
            fromLinkable: true
          })
        ),
        $(
          go.TextBlock,
          {
            margin: 3,
            stroke: "red",
            textAlign: "left"
          },
          new go.Binding("text")
        )
      );

      let generalTemplate = $(
        go.Node,
        "Vertical",
        new go.Binding("isShadowed", "isSelected").ofObject(),
        {
          selectionAdorned: false,
          shadowOffset: new go.Point(0, 0),
          shadowBlur: 15,
          shadowColor: "blue"
        },
        new go.Binding("location", "loc", go.Point.parse),
        $(
          go.Panel,
          "Horizontal",
          { portId: "" },
          $(go.Shape, "TriangleRight", {
            stroke: "#f39c12",
            strokeWidth: 1,
            desiredSize: portSize,
            fill: "white",
            portId: "left",
            toSpot: go.Spot.Left,
            toLinkable: true
          }),
          $(
            go.Shape,
            "Circle",
            {
              fill: "white",
              stroke: "#f39c12",
              width: 70,
              height: 70,
              fromEndSegmentLength: 0,
              toEndSegmentLength: 0
            },
            new go.Binding("fill", "color")
          ),
          $(go.Shape, "TriangleRight", {
            stroke: "#f39c12",
            strokeWidth: 1,
            desiredSize: portSize,
            fill: "white",
            fromSpot: go.Spot.Right,
            portId: "right",
            fromLinkable: true
          })
        ),
        $(
          go.TextBlock,
          {
            margin: 3,
            stroke: "red",
            textAlign: "left"
          },
          new go.Binding("text")
        )
      );

      _this.myDiagram.nodeTemplateMap.add("input", inputTemplate);
      _this.myDiagram.nodeTemplateMap.add("general", inputTemplate);
      _this.myDiagram.nodeTemplateMap.add("output", inputTemplate);
      // _this.myDiagram.nodeTemplateMap.add("extra", extraTemplate);
      // key: _this.create_UUID(),
      // text: _this.componentInfo[prop.type].text,
      // category: _this.componentInfo[prop.type].category,
      // url: _this.componentInfo[prop.type].url,
      // color: "orange"
      // _this.myDiagram.model.nodeDataArray = [
      //   {
      //     key: _this.create_UUID(),
      //     category: "input",
      //     url: "",
      //     color: "orange"
      //   }
      // ];

      _this.myDiagram.linkTemplate = $(
        go.Link,
        {
          routing: go.Link.AvoidsNodes,
          curve: go.Link.JumpOver,
          corner: 3,
          relinkableFrom: true,
          relinkableTo: true,
          selectionAdorned: false, // Links are not adorned when selected so that their color remains visible.
          shadowOffset: new go.Point(0, 0),
          shadowBlur: 5,
          shadowColor: "blue"
        }, // with rounded corners
        $(
          go.Shape,
          new go.Binding("stroke", "isHighlighted", function(h) {
            return h ? "red" : "#2196F3";
          }).ofObject(),
          // the Shape.strokeWidth depends on whether Link.isHighlighted is true
          new go.Binding("strokeWidth", "isHighlighted", function(h) {
            return 1;
          }).ofObject()
        )
      );
      _this.myDiagram.addDiagramListener("ObjectSingleClicked", function(e) {
        var part = e.subject.part;
        if (part instanceof go.Node) {
          let node = { id: part.key, type: part.data.type };
          _this.$emit("on-node-selected", node);
        } else if (part instanceof go.Link) {
          //   _this.parent.onLinkSelection("link");
          _this.$emit("on-link-selected");
        }
      });
      _this.myDiagram.addDiagramListener("LinkDrawn", function(e) {
        let link = {
          fromNode: e.subject.fromNode.data.key,
          toNode: e.subject.toNode.data.key
        };
        _this.$emit("on-link-created", link);
      });

      _this.myDiagram.addDiagramListener("BackgroundSingleClicked", function(
        e
      ) {
        _this.$emit("on-background-selected");
      });
    },
    createNodes() {
      if (
        this.workflow != undefined &&
        this.workflow.components !== undefined
      ) {
        let _this = this;
        this.workflow.components.forEach(component => {
          _this.myDiagram.model.addNodeData({
            key: component.id,
            text: _this.componentInfo[component.type].text,
            type: _this.componentInfo[component.type].type,
            category: "general",
            color: "orange"
          });
        });
      }
    },
    createLinks() {
      if (
        this.workflow != undefined &&
        this.workflow.components !== undefined
      ) {
        let _this = this;
        this.workflow.components.forEach(component => {
          component["input-nodes"].forEach(input => {
            _this.myDiagram.model.addLinkData({
              to: component.id,
              from: input
            });
          });
        });
      }
    },
    create_UUID() {
      var dt = new Date().getTime();
      var uuid = "xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx".replace(
        /[xy]/g,
        function(c) {
          var r = (dt + Math.random() * 16) % 16 | 0;
          dt = Math.floor(dt / 16);
          return (c == "x" ? r : (r & 0x3) | 0x8).toString(16);
        }
      );
      return uuid;
    },
    addNode(prop) {
      let _this = this;
      this.myDiagram.model.addNodeData({
        key: _this.create_UUID(),
        text: _this.componentInfo[prop.type].text,
        type: _this.componentInfo[prop.type].type,
        category: _this.componentInfo[prop.type].category,
        color: "orange"
      });
    }
  }
};
</script>
<style scoped>
.workflow-div {
  height: calc(100vh - 145px) !important;
  border-right: 1px solid #2196f3;
}
.workflow-div >>> canvas {
  outline: none !important;
}
</style>                        