<script>
import jQuery from "jquery";
const $ = jQuery;
export default {
  data() {
    return {
      tab: true,
      item: 1,
      items: [
        { name: "Input Table", url: "/icons/input-white.svg", type: "SOURCE" },
        {
          name: "Output Table",
          url: "/icons/output-white.svg",
          type: "TARGET"
        },
        { name: "Filter", url: "/icons/filter-white.svg", type: "FILTER" },
        { name: "Alter", url: "/icons/alter-white.svg", type: "ALTER" },
        { name: "Join", url: "/icons/join-white.svg", type: "JOIN" }
      ]
    };
  },
  methods: {
    onMenuClick(id) {
      $("#" + id)
        .find("img")
        .css("fill", "red");
    },
    addNode(type) {
      this.$emit("on-node-created", type);
    }
  }
};
</script>

