<template>
  <div class="filter" style="display:flex;flex-direction: column">
    <div class="filterCondition" style="display:flex;flex-direction: row">
      <div style="flex:1">
        <v-autocomplete
          style="padding:8px 4px 8px 16px"
          v-model="columnModel"
          :items="inputColumnNames"
          label="Columns"
          :autofocus="true"
          ref="filtercolumn"
          @change="setFilterFocus()"
        ></v-autocomplete>
      </div>
      <div style="flex:1">
        <v-autocomplete
          style="padding:8px 4px 8px 4px"
          v-model="filterModel"
          :items="filterList"
          label="Filter"
          ref="filtertype"
          @change="setValueFocus()"
        ></v-autocomplete>
      </div>
      <div style="flex:1">
        <v-text-field
          class="filterValue"
          style="padding:8px 16px 8px 4px"
          v-model="valueModel"
          label="Value"
          @keypress="enterpressalert($event)"
          :autofocus="true"
          ref="filtervalue"
        ></v-text-field>
      </div>
    </div>
    <div class="text-center">
      <v-btn class="filter-btn" color="#42aaee" @click="addFilterCondition()">
        <span class="button-label">ADD CONDITION</span>
      </v-btn>
    </div>

    <v-list style="padding-bottom:24px">
      <v-list-item class="filter-list">
        <v-list-item-content>
          <div style="display:flex;flex-direction: row">
            <span class="filterCount">Applied Conditions:</span>
            <span class="roundedCount">{{filterConditionList.length}}</span>
          </div>
        </v-list-item-content>
        <v-list-item-action>
          <v-switch
            color="primary"
            v-model="isAnd"
            :label="getSwitchLabel()"
            class="filter-apply-switch"
          ></v-switch>
        </v-list-item-action>
      </v-list-item>
    </v-list>

    <perfect-scrollbar style="flex:1;border-bottom: 1px solid #ccc;">
      <hr class="pa-0 ma-0" />
      <div
        v-for="(selection,index) in filterConditionList"
        :key="index"
        style="display:flex;flex-direction: column"
      >
        <div
          style="display:flex;flex-direction: row"
          @mouseenter="showButton = index-1"
          @mouseleave="showButton = null"
        >
          <div
            style="flex:1;color:#8a8585;font-size:13px;padding: 12px 8px 12px 16px;"
          >{{selection["field-name"]}}</div>
          <div
            style="flex:1;color:#8a8585;font-size:13px;padding: 12px 8px 12px 8px;"
          >{{selection["condition"]}}</div>
          <div
            style="flex:1;color:#8a8585;font-size:13px;padding: 12px 6px 12px 8px;"
          >{{selection.value}}</div>
          <div style="padding-right: 32px;">
            <v-icon
              v-if="showButton == index-1"
              class="deleteFilterBtn"
              @click="deleteFilter(index)"
            >close</v-icon>
          </div>
        </div>
        <hr class="pa-0 ma-0" />
      </div>
      <v-snackbar class="filter-snackbar" v-model="snackbar" :timeout="3000" :right="true">
        {{ errorMessage }}
        <v-icon color="pink" text @click="snackbar = false">close</v-icon>
      </v-snackbar>
    </perfect-scrollbar>
  </div>
</template>

<script>
import VuePerfectScrollbar from "vue-perfect-scrollbar";

export default {
  components: {
    "perfect-scrollbar": VuePerfectScrollbar
  },
  props: {
    properties: { type: Object, default: () => {} }
  },
  mounted() {
    this.childProperties = { ...this.properties };
    if (this.childProperties["extra-props"] !== undefined) {
      this.childProperties["extra-props"].category =
        this.childProperties === undefined ||
        this.properties["extra-props"].category === undefined
          ? "BASIC"
          : this.childProperties["extra-props"].category;
      this.childProperties["extra-props"]["apply-all"] =
        this.childProperties["extra-props"]["apply-all"] === undefined
          ? this.isAnd
          : this.childProperties["extra-props"]["apply-all"];
    }
  },
  computed: {
    filterList() {
      return this.filters[this.dataType];
    },
    filterConditionList() {
      if (this.childProperties["extra-props"] !== undefined) {
        this.childProperties["extra-props"].condition =
          this.childProperties["extra-props"].condition === undefined
            ? []
            : this.childProperties["extra-props"].condition;
        return this.childProperties["extra-props"].condition;
      } else {
        this.childProperties["extra-props"] = {};
        this.childProperties["extra-props"].condition = [];
        return this.childProperties["extra-props"].condition;
      }
      return [];
    },
    inputColumnNames() {
      let columns = ["Col1", "Col2", "Col3", "Col4"];
      return columns;
    },
    category() {
      if (this.childProperties["extra-props"] !== undefined) {
        this.childProperties["extra-props"].category =
          this.childProperties["extra-props"].category === undefined
            ? "BASIC"
            : this.childProperties["extra-props"].category;
        return this.childProperties["extra-props"].category;
      }
      return "BASIC";
    },
    // filterType() {
    //   return this.isAnd;
    // },
    isValid() {
      return this.valid;
    }
  },
  watch: {
    properties: {
      handler(properties) {
        this.childProperties = { ...properties };
        if (this.childProperties["extra-props"] !== undefined) {
          this.childProperties["extra-props"].category =
            this.childProperties === undefined ||
            this.properties["extra-props"].category === undefined
              ? "BASIC"
              : this.childProperties["extra-props"].category;
          this.childProperties["extra-props"]["apply-all"] =
            this.childProperties["extra-props"]["apply-all"] === undefined
              ? this.isAnd
              : this.childProperties["extra-props"]["apply-all"];
        }
      },
      deep: true
    },
    childProperties: {
      handler(newV) {
        this.$emit("update-data", newV);
      },
      deep: true
    },
    category: {
      handler(newV) {
        console.log("category");
      }
    },
    isAnd: {
      handler(newV) {
        this.childProperties["extra-props"].applyAll = newV;
        this.childProperties = { ...this.childProperties };
        console.log("isAnd: ", newV);
      }
    }
  },
  data() {
    return {
      valid: false,
      columnModel: "",
      filterModel: "",
      valueModel: "",
      errorMessage: "",
      snackbar: false,
      showButton: null,
      isAnd: true,
      dataType: "OTHERS",
      filters: {
        NUMERIC: [">", "<", "="],
        OTHERS: [
          "equals",
          "does not equal",
          "contains",
          "does not contain",
          "begins with",
          "does not begin with",
          "ends with",
          "does not end with"
        ]
      },
      childProperties: {}
    };
  },
  methods: {
    getSwitchLabel() {
      return this.isAnd ? "Apply All" : "Apply Any";
    },
    addFilterCondition() {
      if (
        this.columnModel !== "" &&
        this.filterModel !== "" &&
        this.valueModel !== ""
      ) {
        let newItem = {
          "field-name": this.columnModel,
          condition: this.filterModel,
          value: this.valueModel
        };
        if (this.childProperties["extra-props"] === undefined) {
          this.childProperties["extra-props"] = {};
          this.childProperties["extra-props"].condition = [];
          this.childProperties["extra-props"].condition.push(newItem);
          this.valid = true;

          this.childProperties = { ...this.childProperties };
          return;
        }

        let doAdd = true;
        for (
          let i = 0;
          i < this.childProperties["extra-props"].condition.length;
          i++
        ) {
          let currentItem = this.childProperties["extra-props"].condition[i];
          if (
            currentItem["field-name"] === this.columnModel &&
            currentItem["condition"] === this.filterModel &&
            currentItem.value === this.valueModel
          ) {
            doAdd = false;
            this.errorMessage = "Duplicate filter condition";
            this.snackbar = true;
            break;
          }
        }
        if (doAdd) {
          this.childProperties["extra-props"].condition.push(newItem);
          this.columnModel = "";
          this.filterModel = "";
          this.valueModel = "";
        }
      } else {
        this.errorMessage = "Not a valid filter condition";
        this.snackbar = true;
      }
      this.valid = true;
      this.childProperties = { ...this.childProperties };
      return;
    },
    enterpressalert($event) {
      if ($event.charCode == 13) {
        this.addFilterCondition();
        this.$refs.filtercolumn.focus();
      }
    },
    deleteFilter(item) {
      this.childProperties["extra-props"].condition.splice(item, 1);
      if (this.childProperties["extra-props"].condition.length === 0) {
        this.valid = false;
      }
    },
    setFilterFocus() {
      this.$refs.filtertype.focus();
    },
    setValueFocus() {
      this.$refs.filtervalue.focus();
    }
  }
};
</script>

<style scoped>
.filter {
  height: 100% !important;
}

.filter-btn {
  height: 30px;
}
.deleteFilterBtn {
  padding: 12px 16px 0px 0px;
  position: fixed;
  z-index: 30;
}
.deleteFilterBtn.v-icon {
  font-size: 20px !important;
}

.roundedCount {
  text-decoration: none;
  border-radius: 25px;
  width: 15px;
  height: 15px;
  background: #42aaee;
  color: white;
  text-align: center;
  font-size: 11px;
  margin-top: 4px;
}

.filterCount {
  text-decoration: none;
  color: #42aaee;
  padding-right: 6px;
}

.sub_div {
  position: absolute;
  bottom: 0px;
  margin-bottom: 0px;
  color: #ff0000;
}

.text-center {
  padding: 24px 10px 32px 10px;
}

.filterCondition {
  padding-top: 30px !important;
}
.filterValue {
  padding: 0px;
}

.button-label {
  color: white;
}

.filter-list .v-list__tile {
  height: 27px;
  font-size: 13px;
  padding: 0px 12px 0px 16px !important;
}

.filter-snackbar .v-snack__content {
  height: 32px;
  padding: 14px 16px;
}

.filter-apply-switch .v-input--selection-controls__input {
  margin: 3px 0px 0px 0px !important;
  position: absolute;
  left: -35px;
}
.filter-apply-switch .v-input--switch__track {
  height: 8px !important;
  left: 7px !important;
  width: 16px !important;
}
.filter-apply-switch .v-label {
  font-size: 12px;
  margin: 0;
}
.filter-apply-switch .v-input--switch__thumb {
  height: 14px !important;
  width: 14px !important;
}
</style>