<template>
  <v-container fluid grid-list-md>
    <!-- <Menu @on-node-created="addNode" @refresh-diagram="refreshDiag=!refreshDiag"></Menu> -->
    <div class="working-area">
      <Menu @on-node-created="addNode" @refresh-diagram="refreshDiag=!refreshDiag"></Menu>
      <v-layout row class="pa-0 ma-0">
        <v-flex xs8 md8 class="pa-0 ma-0">
          <GoJsDiagram
            :addedNodeType="addedNodeType"
            :refreshDiag="refreshDiag"
            :workflow="workflow"
            @on-node-selected="onNodeSelection"
            @on-node-deleted="onNodeDeletion"
            @on-link-selected="onLinkSelection"
            @on-link-created="onLinkCreation"
            @on-link-deleted="onLinkDeletion"
            @on-background-selected="onBackgroundSelection"
          ></GoJsDiagram>
        </v-flex>
        <v-flex xs4 md4 class="pa-0 ma-0">
          <div class="diagram-height">
            <RightPanel @update-data="updateData" :dynamicComponent="selectedComponent"></RightPanel>
          </div>
        </v-flex>
      </v-layout>
    </div>
  </v-container>
</template>
<script>
import Menu from "./Menu";
import RightPanel from "./RightPanel";
// import { Workflow } from "./WorkflowView.js";
import GoJsDiagram from "./gojs-diagram";
import PostService from "../service.js";

export default {
  components: {
    Menu,
    RightPanel,
    GoJsDiagram
  },
  async created() {
    // console.log("created");
    try {
      this.workflow = await PostService.getWorkflow(this.workflowId);
      console.log(this.workflow);
    } catch (error) {
      console.log(error);
    }
  },
  data() {
    return {
      selectedComponent: {},
      workflow: null,
      workflowId: "5d9884d7ba6da910f062ac55",
      addedNodeType: {},
      refreshDiag: false,
      componentInfo: {
        SOURCE: {
          text: "Source",
          type: "SOURCE",
          category: "input",
          url: "/icons/inout-white.svg"
        },
        TARGET: {
          text: "Target",
          type: "TARGET",
          category: "output",
          url: "/icons/output.svg"
        },
        FILTER: {
          text: "Filter",
          type: "FILTER",
          category: "general",
          url: "/icons/filter.svg"
        },
        JOIN: {
          text: "Join",
          type: "JOIN",
          category: "general",
          url: "/icons/join.svg"
        },
        ALTER: {
          text: "Alter",
          type: "ALTER",
          category: "general",
          url: "/icons/alter.svg"
        }
      }
    };
  },
  methods: {
    updateData(data) {
      let index = this.workflow.components.findIndex(component => {
        return component.id === data.id;
      });
      if (index > -1) {
        this.workflow.components[index] = data;
      }
    },
    onNodeSelection(node) {
      if (this.workflow.components !== undefined) {
        let component = this.workflow.components.find(component => {
          return component.id === node.id;
        });
        if (!component) {
          component = {
            id: node.id,
            type: node.type,
            "output-nodes": [],
            "input-nodes": [],
            "input-fields": [],
            "output-fields": [],
            "extra-props": {}
          };
          this.workflow.components.push(component);
        }
        this.selectedComponent = component;
        this.saveWorkflow();
      }
    },
    async saveWorkflow() {
      try {
        if (this.workflow === null) {
          this.workflow = await PostService.saveWorkflow(this.workflow);
        } else {
          await PostService.saveWorkflow(this.workflow);
        }
      } catch (error) {
        console.log(error);
      }
    },
    onNodeDeletion(text) {
      let component = {
        type: "Empty"
      };
      this.selectedComponent = component;
    },
    onLinkSelection(text) {
      let component = {
        type: "Empty"
      };
      this.selectedComponent = component;
    },
    onLinkCreation(link) {
      let fromNode = this.workflow.components.find(component => {
        return component.id === link.fromNode;
      });
      fromNode["output-nodes"].push(link.toNode);
      let toNode = this.workflow.components.find(component => {
        return component.id === link.toNode;
      });
      toNode["input-nodes"].push(link.fromNode);
      this.saveWorkflow();
    },
    onLinkDeletion(link) {
      console.log(link);
    },
    onBackgroundSelection() {
      let component = {
        type: "Empty"
      };
      this.selectedComponent = component;
    },
    addNode(type) {
      this.addedNodeType = { type: type };
    }
  }
};
</script>

<style>
.diagram-height {
  height: calc(100vh - 145px) !important;
}
.working-area {
  border: 1px solid #2196f3;
  /* border-radius: 5px !important; */
  height: calc(100vh - 92px) !important;
}
</style>

