import axios from "axios";
import { async, reject } from "q";
import { resolve } from "path";

const url = "http://" + document.location.hostname + ":5000/api/workflows";

class PostService {
  //get post
  static getWorkflow(id) {
    console.log("id: ", id);
    return new Promise(async (resolve, reject) => {
      try {
        const res = await axios.get(url + "/" + id);
        const data = res.data;
        resolve(data);
      } catch (err) {
        reject(err);
      }
    });
  }

  // save workflow
  static saveWorkflow(params) {
    console.log("id: ", params["_id"]);
    return new Promise(async (resolve, reject) => {
      try {
        const res = await axios.post(url + "/" + params["_id"], params);
        resolve("Saved successfully");
      } catch (err) {
        reject(err);
      }
    });
  }

  // //create post
  // static saveWorkflow(id,workflow){
  //    return axios.post(url+"/workflow/"+id,{...workflow})
  // }
}
export default PostService;
